"use client";

import Link from "next/link";
import Image from "next/image";
import fs from "fs";
import path from "path";

type BlogPost = {
  slug: string;
  title: string;
  description: string;
  date: string;
  image: string;
};

// Generate blog post metadata dynamically
const getBlogPosts = (): BlogPost[] => {
  const dataDir = path.join(process.cwd(), "app", "blog", "data");
  if (!fs.existsSync(dataDir)) return [];

  const files = fs.readdirSync(dataDir).filter((f) => f.endsWith(".md"));

  // For demo: generate basic description and date
  return files.map((file, idx) => ({
    slug: file.replace(".md", ""),
    title: file.replace(".md", "").replace(/([A-Z])/g, " $1").trim(),
    description: "Click to read full blog post.",
    date: "October 2025",
    image: "/Kothi1.png", // put image in public/
  }));
};

export default function BlogPage() {
  const blogPosts = getBlogPosts();

  return (
    <div className="min-h-screen bg-gray-50 py-20 px-6 md:px-12">
      <h1 className="text-4xl font-bold text-center text-[#FE904E] mb-12">
        Our Blog
      </h1>

      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
        {blogPosts.map((post) => (
          <div
            key={post.slug}
            className="bg-white shadow-lg rounded-2xl overflow-hidden hover:shadow-2xl transition-shadow"
          >
            <div className="relative w-full h-56">
              <Image
                src={post.image}
                alt={post.title}
                fill
                className="object-cover rounded-t-2xl"
              />
            </div>
            <div className="p-6">
              <h2 className="text-2xl font-semibold text-gray-800 mb-2">
                {post.title}
              </h2>
              <p className="text-sm text-gray-500 mb-3">{post.date}</p>
              <p className="text-gray-600 mb-4">{post.description}</p>
              <Link
                href={`/blog/${post.slug}`}
                className="text-[#FE904E] font-medium hover:underline"
              >
                Read More â†’
              </Link>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
